@import '~@/styles/variables.scss';

.root {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;

	$ItemMarginTop: calc(1rem + #{$header-height});

	.main {
		flex: 1;
		margin-top: $ItemMarginTop;
		max-width: 50vw;
		margin-right: 1rem;
		position: relative;
	}
	.aside {
		width: 16rem;
		margin-top: $ItemMarginTop;
	}
}
.toTop {
	position: fixed;
	right: 1rem;
	bottom: 1rem;
}

@media (max-width: 1000px) {
	.root {
		flex-direction: column;
		justify-content: flex-start;
		align-items: stretch;

		.main {
			margin-right: 0;
			max-width: 100vw;
			margin-top: $header-height;
		}
		.aside {
			margin: 1rem;
			width: auto;
		}
	}
	.toTop {
		right: 0.5rem;
		bottom: 3em;
	}
}
